body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  -webkit-user-select: none;

     -moz-user-select: none;

      -ms-user-select: none;

          user-select: none;

  background: #1A0D39;
}

:not(:root):-webkit-full-screen{
  background: #1A0D39;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  outline: none;
}

#startWrapper{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 40px;
}

#startWrapper h1{
  font-size: 2em;
  margin-bottom: 0.3em;
  line-height: 0;
  font-family: cursive;
  color: goldenrod;
  margin-bottom: .8em;
}

#startWrapper p{
  font-size: 1.3em;
  margin-bottom: 0.3em;
  color: #695c86;
  margin-bottom: 1.5em;
  padding: 0 2em;
  display: block;
  flex-wrap: wrap;
  text-align: center;
  line-height: 1.5em;
}

#startBtn, .video-player{
  background: #3b3052;
  padding: 0.8em 1.2em;
  max-width: 200px;
  font-size: 0.9em;
  border: none;
  color: #bfb8ce;
}

.video-player{
  margin-top: 1.5em;
  position: absolute;
  bottom: 5em;
  right: 0;
  left: 0;
  margin: 0 auto;
}

#mainApp{
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100vw;
  height: 100vh;
}

#slideIndex{
  position: fixed;
  top: 1em;
  left: 1em;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: 2px solid #ddd;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}

#slideshowTime{
  position: fixed;
  top: 1.6em;
  right: 0.85em;
  display: flex;
  align-items: center;
}

#slideshowTime span{
  font-size: 1.3em;
  color: #fff;
  padding: 0 0.1em;
  letter-spacing: 0.04em;
}

#slideshowTime span:nth-child(2){
  margin-bottom: 0.13em;
}

.blink{
  -webkit-animation: blink 1s steps(1) infinite;
          animation: blink 1s steps(1) infinite;
}

@-webkit-keyframes blink{
  0%{
    opacity: 0;
  }
  50%{
    opacity: 1;
  }
}

@keyframes blink{
  0%{
    opacity: 0;
  }
  50%{
    opacity: 1;
  }
}

.round-btn{
  background: #ffae00;
  height: 150px;
  width: 150px;
  border-radius: 50%;
  margin: auto;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.5);
  border: none;
  outline: none;
  font-size: 1.5em;
  text-align: center;
  letter-spacing: 0.15em;
}

.round-btn:active,
.round-btn:hover{
  box-shadow: none;
}

#buzzer{
  position: fixed;
  bottom: 170px;
  left: 0;
  right: 0;
}

#joinerBtn{
  position: fixed;
  top: 0;
  bottom: 2em;
  left: 0;
  right: 0;
}
#setupScreen{
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
}

#setupScreen .actions{
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 50px;
}

#timePicker{
    position: relative;
    width: 100vw;
    display: flex;
    justify-content: center;
}

#circular-slider{
    margin-left: -15vw;
    margin-top: -10vw;
}

#timePicker #time{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    color: #fff;
    font-size: 2em;
    text-align: center;
}

#timePicker #time span{
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 28vw;
    text-align: center;
    letter-spacing: 0.05em;
}

#timePicker #time button{
    border: none;
    background: rgba(255, 255, 255, 0.1);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 0.55em;
    text-align: center;
    letter-spacing: 0.18em;
    padding: 0.6em 0.8em;
    margin-top: 0.5em;
    margin-bottom: 0.5em;
    z-index: 1;
    outline: none;
}

#setupScreen .tabs{
    display: inline-flex;
    margin: 0 auto;
    align-items: center;
    position: relative;
    margin-bottom: 2em;
    border-radius: 50px;
    overflow: hidden;
    max-width: 300px;
}

#setupScreen .tabs button{
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    flex: 1 1;
    
    border: none;
    text-transform: uppercase;
    padding: 0.75em 1.5em;
    font-size: 1.2em;
    background: #9a92b1;
}

#setupScreen .tabs button:not(.active){
    color: #766a91;
}

#setupScreen .tabs span{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 1;
    height: 25px;
    width: 1px;
    background-color: #766a91;
}
#slideStack{
    overflow-x: auto;
    flex: 1 1;
    position: relative;
}

#slideStack #scroller{
    position: relative;
    height: 100%;
}

#Slide{
    position: absolute;
    top: 0;
    bottom: 40px;
    margin: auto;
    width: 100vw;
    height: 40%;
    z-index: 1;
}

#Slide:not(.current){
    opacity: 0;
    z-index: 0;
    pointer-events: none;
}
#Slide{
    background: #fff;
    color: #555;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#Slide.moved{
    transition: -webkit-transform 0.1s ease-out;
    transition: transform 0.1s ease-out;
    transition: transform 0.1s ease-out, -webkit-transform 0.1s ease-out;
}
